.imagenGenero{
    position: absolute;
    height: inherit;
    width: inherit;
    z-index: -1;
    padding: 15px;
}
.imagenGenero img{
    border-radius: 8px;
    width: inherit;
    height: inherit;
    filter: brightness(60%);
}
.swiper-slide2 {
    box-sizing: border-box;
    color: white;
    height: 46vw;
    border-radius: 8px;
    
    -webkit-box-shadow: 0px 79px 47px -81px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 79px 47px -81px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 79px 47px -81px rgba(0, 0, 0, 0.75);
    font-size: 0.8rem;
    z-index: 999;
  }
  .swiper-slide p{
      z-index: 9999;
  }
  .swiper-slide:hover img{
    content:url("../../assets/checked.png");
    background-color: black;
    height: auto;
    width: inherit;
    margin-top: 55%;
    margin-left: 30%;
    width: 70%;
    filter: grayscale();
  }
  .swiper-slide:hover{
      background-color: black;
  }